.comment-area {
    form {
        display: inline-flex;
        border-top: 1px solid #dbdbdb;
        .add-comment-textarea {
            width: 30rem;
            color: #8e8e8e;
            padding: rem(16px);
            border: 0;
            border-bottom: 1px solid #dbdbdb;
            display: inline;
            font-family: inherit;
        }

        .add-comment-btn {
            background-color: transparent;
            border: 0;
            color: #0095f6;
            font-weight: 600;
            font-size: 14px;
            flex-direction: row-reverse;
        }
    .avatar{
        margin: auto;
        margin-left: 1rem;
    }
    }
}
