.chat-cmp {
    position: fixed;
    background-color: gray;
    min-height: 145px;
    top: 108px;
    right: 75%;
}
.chat-icon {
    right: 40px;
    position: fixed;
    bottom: 40px;
    font-size: 3em;
    cursor: pointer;
    transition: 0.2s all;
    &:hover {
        transform: scale(1.2);
    }
}
